<script setup lang="ts">
import useAsync from "~/composables/use-async";
import EventsAPI from "~/services/events";
import { getLineBreaksStyle } from "~/utils/typography";

const { t } = useI18n();
const route = useRoute();

const { data: event, error: eventError, loading: loadingEvent, run: loadEvent } = useAsync(EventsAPI.loadById(route.params.eventId as string));
const {
  data: statsData,
  error: statError,
  loading: loadingStats,
  run: loadStats,
} = useAsync(EventsAPI.stats.getEventScoreStatsById(route.params.eventId as string));
const breadcrumbs = computed(() => [
  { to: `/events/${event.value?.id}`, title: `${event.value?.team1Name} vs ${event.value?.team2Name}` || t("labels.go_to_event") },
  { to: `/events/stats/${event.value?.id}`, title: t("labels.statistics"), active: true },
]);

const error = computed(() => eventError.value || statError.value);
const scoreStats = computed(() => statsData.value?.scores);
const gameStats = computed(() => statsData.value?.overview);
const loading = computed(() => loadingStats.value || loadingEvent.value);

onMounted(() => {
  Promise.all([
    loadEvent(),
    loadStats(),
  ]);
});

definePageMeta({ layout: "home" });
</script>

<template>
  <div class="flex flex-col items-center px-4 py-5 md:px-10">
    <div class="w-full sm:w-full xl:w-2/3">
      <BaseBreadcrumbs class="mb-6" :items="breadcrumbs" />
      <template v-if="error">
        <BaseNotFound />
      </template>
      <template v-else-if="event?.geniusId">
        <EventStatsGenius :id="event.geniusId" />
      </template>

      <template v-else>
        <EventCardBoard
          class="mb-6 min-h-[300px]"
          :title="t('pages.events.tabs.event_stats.score_board_title')"
          :is-loading="loading"
          :no-data-text="scoreStats ? '' : t('pages.events.tabs.event_stats.empty_stats_data')"
        >
          <EventStatsScoreboard
            :team1-name="event?.team1Name"
            :team2-name="event?.team2Name"
            :team1-logo-url="event?.team1LogoUrl"
            :team2-logo-url="event?.team2LogoUrl"
            :team1-color="event?.team1Color"
            :team2-color="event?.team2Color"
            :team1-score="scoreStats?.team_home.total || gameStats?.team_home.total"
            :team2-score="scoreStats?.team_away.total || gameStats?.team_away.total"
            full-size
          />

          <div role="separator" class="hr" style="background: var(--base-background-color)" />

          <EventStatsTableScore
            :team1-logo-url="event?.team1LogoUrl"
            :team2-logo-url="event?.team2LogoUrl"
            :team1-color="event?.team1Color"
            :team2-color="event?.team2Color"
            :stats-data="scoreStats"
            full-size
          >
            <template #default="{ row }">
              <div>
                <BaseLogo
                  :title="row.name"
                  :alt="row.name || row.alt"
                  :src="row.logoUrl"
                  :icon-background="row.logoBg"
                  :width="36"
                  :height="36"
                  no-background
                  with-initials
                  fit="contain"
                />
              </div>
              <span class="hidden mx-2 text-base font-medium break-all sm:block line-clamp-1">
                {{ row.name }}
              </span>
            </template>
          </EventStatsTableScore>
        </EventCardBoard>

        <EventCardBoard
          class="min-h-[300px]"
          :title="t('pages.events.tabs.event_stats.game_board_title')"
          :is-loading="loading"
          :no-data-text="gameStats ? '' : t('pages.events.tabs.event_stats.empty_stats_data')"
        >
          <EventStatsTableGame
            :team1-name="event?.team1Name"
            :team2-name="event?.team2Name"
            :team1-logo-url="event?.team1LogoUrl"
            :team2-logo-url="event?.team2LogoUrl"
            :team1-color="event?.team1Color"
            :team2-color="event?.team2Color"
            :stats-data="gameStats"
            full-size
          >
            <template #column-home-team="{ title, alt, src, iconBackground }">
              <BaseLogo
                class="shrink-0"
                :title="title"
                :alt="alt || t('labels.team_1_logo')"
                :src="src"
                :icon-background="iconBackground"
                :width="48"
                :height="48"
                no-background
                with-initials
                fit="contain"
              />
              <span class="normal-case text-base text-[var(--base-tab-text-color)]" :class="getLineBreaksStyle(title)">{{ title }}</span>
            </template>
            <template #column-away-team="{ title, alt, src, iconBackground }">
              <BaseLogo
                class="shrink-0"
                :title="title"
                :alt="alt || t('labels.team_2_logo')"
                :src="src"
                :icon-background="iconBackground"
                :width="48"
                :height="48"
                no-background
                with-initials
                fit="contain"
              />
              <span class="normal-case text-base text-[var(--base-tab-text-color)]" :class="getLineBreaksStyle(title)">{{ title }}</span>
            </template>
          </EventStatsTableGame>
        </EventCardBoard>
      </template>
    </div>
  </div>
</template>
